.application {
  height: 100%;
  color: #e6e6e6;
  overflow: scroll;
  padding: 1rem 1rem 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
}

.application .box {
  margin-bottom: 1rem;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
  -ms-flex-preferred-size: 100%;
      flex-basis: 100%;
  -ms-flex-positive: 1;
      flex-grow: 1;
  width: 384px;
  min-height: 300px;
}

@media (min-width: 50rem) {
  .application .box {
    -ms-flex-preferred-size: 33%;
        flex-basis: 33%;
  }
}

@media (min-width: 80rem) {
  .application .box {
    -ms-flex-preferred-size: 25%;
        flex-basis: 25%;
  }
}

@media (min-width: 110rem) {
  .application .box {
    -ms-flex-preferred-size: 20%;
        flex-basis: 20%;
  }
}

.center {
  margin-left: auto;
  margin-right: auto;
  text-align: center;
}

.pricedate {
  margin-top: 0;
  margin-bottom: -0.25rem;
  font-size: 12px;
  color: #cecdcd;
}
.price {
  margin-top: 0;
  margin-bottom: 1rem;
}

.trend-up {
  width: 0;
  height: 0;
  border-left: 20px solid transparent;
  border-right: 20px solid transparent;

  border-bottom: 20px solid #00f700;
}
.trend-down {
  width: 0;
  height: 0;
  border-left: 20px solid transparent;
  border-right: 20px solid transparent;

  border-top: 20px solid #f00;
}
.trendingbox .box__content {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.types {
  margin: 0 auto;
}

input[type="radio"] {
  position: absolute;
  left: -9999px;
}

input[type="radio"] + label {
  border: 1px solid rgba(0, 204, 204, 0.7);
  border-left: none;
  padding: 0.5rem 1rem;
  cursor: pointer;
  background-color: rgba(0, 104, 104, 0.5);
}
input[type="radio"] + label:first-of-type {
  border-top-left-radius: 2px;
  border-bottom-left-radius: 2px;
  border-left: 1px solid rgba(0, 204, 204, 0.7);
}
input[type="radio"] + label:last-of-type {
  border-top-right-radius: 2px;
  border-bottom-right-radius: 2px;
}

input[type="radio"]:checked + label {
  padding: 0.5rem 1rem;
  cursor: pointer;
  background-color: rgb(57, 158, 158);
}


